.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
	padding: 15px 32px;
	
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    font-family: Arial;
    font-weight: bold;
    margin: 20px 10px 20px 0px;
    cursor: pointer;
    border-radius: 8px;
    -moz-border-radius: 8px;
    -webkit-border-radius: 8px;
    box-shadow: 3px 4px 0px 0px rgba(134, 134, 134, 0.5);


  }

